const text = ref(`## 1 拨打电话、话单查询、话单状态推送、录音下载

### 1.1 拨打电话

**接口功能**
用户拨打电话

**使用说明**
调用接口拨打电话

**接口类型**

| **请求方法** | POST(body)                                         |
| ------------ | -------------------------------------------------- |
| **访问url**  | **<https://XXXX/agent-system-rest/proxyapi/call>** |
| **通信协议** | **HTTPS**                                          |

**请求参数**

| **参数名称**    | **是否必选** | **参数类型**      | **说明**                                                     |
| --------------- | ------------ | ----------------- | ------------------------------------------------------------ |
| **callerNbr**   | **是**       | **String(1-20)**  | **主叫号码**                                                 |
| **calleeNbr**   | **是**       | **String(1-20)**  | **被叫号码**                                                 |
| **callerIp**    | **是**       | **String(1-20)**  | **用户端IP**                                                 |
| **statusUrl**   | **否**       | **String(1-512)** | **通话状态推送地址（不需要状态数据，可以不填）**             |
| **cdrUrl**      | **否**       | **String(1-512)** | **话单推送地址（不需要话单，可以不填）**                     |
| **ext1**        | **否**       | **String(1-256)** | **用户扩展字段（该字段数据在话单推送和状态推送，携带该字段数据）** |
| **companyCode** | **是**       | **String**        | **子公司code（在数企注册的子公司code）**                     |
| **callbackUrl** | **是**       | **String(1-512)** | **通话唯一id推送地址**                                       |

**响应参数**

| **参数** | **参数类型**     | 说明           |
| -------- | ---------------- | -------------- |
| data   | **String(1-50)** | **请求唯一ID** |

**接口示例**

* 请求示例

  \`\`\`json
  {
  "companyCode": "F**m",
  "statusUrl": "https://1.1.1.1:8311/call/status",
  "callerNbr": "1871****678",
  "callerIp": "127.0.0.1",
  "calleeNbr": "189****3333",
  "cdrUrl": "http://1.1.1.1:8311/call/cdr",
  "ext1": "你好",
  "callbackUrl": "http://1.1.1.1:8311/call/cdr"

  }
  \`\`\`


* 响应示例

  \`\`\`json
      {
      "msg": null,
      "statusCode": 0,
      "data": "callId12312379369728",
      "timestamp": 1693988408184
      }
  \`\`\`

### 1.2 通话状态推送

**接口功能**

通话状态推送

**使用说明**

调用接口拨打电话携带statusUrl参数

**接口类型**

| **请求方法** | POST(body) |
| ------------ | ---------- |
| **访问url**  | **无**     |
| **通信协议** | **HTTPS**  |

**推送参数**

| **参数名称**  | **是否必选** | **参数类型**      | **说明**                                                     |
| ------------- | ------------ | ----------------- | ------------------------------------------------------------ |
| baduSessionId |              | **String(1-50)**  | 通话唯一标识                                                 |
| ext1          |              | **String(1-256)** | 用户扩展参数                                                 |
| eventType     |              | **String(1-20)**  | 事件类型；caller\\_answer主叫应答；callee\\_answer被叫应答；caller\\_disconnect主叫挂断；callee\\_disconnect被叫挂断 |
| timestamp     |              | String(1-128)     | 确发时间 yyyy-MM-dd HH\\:mm\\:ss                               |

**用户端响应参数**
响应 http编码=200

**接口示例**

* 推送参数示例

\`\`\`json
{
  "baduSessionId": "169934****243263488",
  "eventType": "caller_answer",
  "ext1": "你好",
  "timestamp": "2023-07-01 12:13:23"
}
\`\`\`

### 1.3 通话话单推送

**接口功能**

通话话单推送

**使用说明**

调用接口拨打电话携带**cdrUrl**参数

**接口类型**

| **请求方法** | POST(body) |
| ------------ | ---------- |
| **访问url**  | **无**     |
| **通信协议** | **HTTPS**  |

**推送参数**

| **参数名称**   | **是否必选** | **参数类型**     | **说明**                                 |
| -------------- | ------------ | ---------------- | ---------------------------------------- |
| eventType      |              | String(1-50)     | 事件类型；固定值cdr                      |
| data           |              | 话单集合         |                                          |
| baduSessionId  |              | **String(1-50)** | 通话唯一标识                             |
| callerNum      |              | **String(1-20)** | 主叫号码                                 |
| calleeNum      |              | **String(1-20)** | 被叫号码                                 |
| connectTime    |              | String(1-128)    | 通话开始时间 yyyy-MM-dd HH\\:mm\\:ss       |
| disconnectTime |              | String(1-128)    | 通话结束时间                             |
| timeConsume    |              | String           | 通话时长单位秒                           |
| status         |              | String           | 通话状态1=主叫挂单；2=被叫挂断；3=已接通 |
| ext1           |              | String(1-256)    | 用户扩展参数                             |
| recordDownUrl  |              | String(1-512)    | 录音下载url                              |

**用户端响应参数**

&#x20;响应 http编码=200

**接口示例**

* 推送参数示例

\`\`\`json
{
  "msg": null,
  "statusCode": 0,
  "data": {
    "data": [
      {
        "callerNum": "18922223333",
        "recordDownUrl": "https://xxxxx/test?auth=fff",
        "disconnectTime": "2023-07-01 12:12:40",
        "timeConsume": "17",
        "baduSessionId": "1699342953899618304",
        "connectTime": "2023-07-01 12:12:23",
        "calleeNum": "18711112222",
        "status": "3"
      },
      {
        "callerNum": "18122223333",
        "recordDownUrl": "https://xxxxx/test?auth=aaaa",
        "disconnectTime": "2023-07-01 12:13:40",
        "timeConsume": "17",
        "baduSessionId": "1699342953899618305",
        "connectTime": "2023-07-01 12:13:23",
        "calleeNum": "18911112222",
        "status": "3"
      }
    ],
    "eventType": "cdr"
  },
  "timestamp": 1693988408184
}
\`\`\`

### 1.4 录音下载

**接口功能**&#x20;

录音下载

