**接口类型**

| **请求方法** | get       |
| ------------ | --------- |
| **访问url**  | **无**    |
| **通信协议** | **HTTPS** |

**推送参数**

无

**用户端响应参数**

无

**接口示例**

无

### 1.5 查询话单

**接口功能**

根据baduSessionId获取话单

**使用说明**&#x20;

调用该接口获取一条话单记录

**接口类型**

| **请求方法** | post(body)                                                   |
| ------------ | ------------------------------------------------------------ |
| **访问url**  | [https://XXXX](https://XXXX/md-into-call/proxyapi/call)/md-call-cdrpush/cdr/getOne |
| **通信协议** | **HTTPS**                                                    |

**请求参数**

| **参数名称**  | **是否必选** | **参数类型** | **说明**                          |
| ------------- | ------------ | ------------ | --------------------------------- |
| baduSessionId | **是**       | **String**   | **通话唯一标识**                  |
| time          | **是**       | **String**   | 通话时间；格式yyyyMM；例如：20221 |

**响应参数**

与话单推送中，推送参数一致

**接口示例**

* 请求示例

\`\`\`json
{
  "baduSessionId": "1699342953899618304",
  "time": "202307"
}
\`\`\`

* 响应示例

\`\`\`json
{
  "msg": null,
  "statusCode": 0,
  "data": {
    "data": [
      {
        "callerNum": "18922223333",
        "recordDownUrl": "https://xxxxx/test?auth=fff",
        "disconnectTime": "2023-07-01 12:12:40",
        "timeConsume": "17",
        "baduSessionId": "1699342953899618304",
        "connectTime": "2023-07-01 12:12:23",
        "calleeNum": "18711112222",
        "status": "3"
      }
    ],
    "eventType": "cdr"
  },
  "timestamp": 1693988408184
}
\`\`\`

### 1.6 通话唯一id推送

**接口功能**

通话唯一id推送

**使用说明**

调用接口拨打电话携带callbackUrl参数

**接口类型**

| **请求方法** | POST(body) |
| ------------ | ---------- |
| **访问url**  | **无**     |
| **通信协议** | **HTTPS**  |

**推送参数**

| **参数名称** | **是否必选** | **参数类型**     | **说明**     |
| ------------ | ------------ | ---------------- | ------------ |
| callId       |              | **String(1-50)** | 请求唯一标识 |
| sessionId    |              | **String(1-50)** | 通话唯一标识 |

**用户端响应参数**
响应 http编码=200

**接口示例**

* 推送参数示例

* 响应示例

  \`\`\`json
       {
      "msg": null,
      "statusCode": 0,
      "data": {
      "sessionId": "badu169933****079369728",
      "callId": "callid079369728",
      "ext1": "扩展字段"
      },
      "timestamp": 1693988408184
      }
  \`\`\`

